.top button{
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 20px 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 12px;
    margin: 10px
  }
  
  .divider{
      width:50px;
      height:auto;
      display:inline-block;
  }
  
  .table1{
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .td1, .th1 {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
    color: white;
    
  }
  
  .tr1:nth-child(even) {
    background-color: #dddddd;
  }
  
  .top{
    background-color: #282c34;
    min-height: 100vh;
    overflow: auto;
    width: 100%;
  }
  
  
  
  .row {
    display: flex;
  }
  
  /* Create four equal columns that sits next to each other */
  .column {
    flex: 25%;
    max-width: 19%;
    flex-shrink: 1;
  }
  
  .img-item{
    width: 200px;
    height: 200px;
    margin-bottom: 30px;
  }
  
  .albums{
    width: 170px;
    height: 170px;
    margin: 0px;
  }
  
  .details h3{
    margin: 1px;
    color: white;
    font-weight: normal;
    font-size: small;
  }
  
  .details p{
    margin: 1px;
    color: grey;
    font-size: small;
  }